.carousel-item {
    height: 75vh;
    /* vh stands for viewport height and 75 means that 75% of the height of the viewport will be taken up by the carousel-item. This is no matter the screen size, it will always take 75% of the screen size height. */
}

.carousel-caption {
    background-image: radial-gradient(circle at 50% 50%, rgba(200, 200, 200, .75), transparent);
    color: #000 !important;
}

/* Below we remove the next/previous text from around the carousel next/previous arrows. */
span.visually-hidden {
    display: none;
}

/* The styling below will keep the carousel-caption from riding down the UI on a smaller screen size. */
@media only screen and (max-width: 1023px){
    .carousel-item {
        height: unset;
        /* unset is the way in which we can programmatically undo previous stylings that have been set. */
    }
}


